export const deletePersFromRole =params =>{ return axios.delete(`${promote}roles/authorities/`+params).then(res =>res.data);}

export const addPersToRole =(roleId,params) =>{ return axios.post(`${promote}roles/authorities/`+roleId,params).then(res => res.data);}


