<template>
    <section>
        <el-row>
            <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
                <el-form :inline="true">
                    <el-form-item style="float:left">
                        <el-button type="primary" @click="addRole">新增</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
                <el-table
                        :data="permissions"
                        border
                        style="width:100%">
                    <el-table-column
                            prop="permissionId"
                            label="权限ID"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="authority"
                            label="权限名称"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="description"
                            label="权限描述">
                    </el-table-column>
                    <el-table-column
                            label="操作" align="center">
                        <template scope="scope">
                            <el-button-group>
                                <el-button
                                        size="small"
                                        type="warning"
                                        @click="EditRole(scope.$index, scope.row)">编辑</el-button>
                                <el-button
                                        size="small"
                                        type="danger"
                                        @click="deleteRole(scope.$index, scope.row)">删除</el-button>
                            </el-button-group>
                        </template>

                    </el-table-column>
                </el-table>

            </el-col>
        </el-row>
        <!--新增界面-->
        <el-dialog title="添加权限" v-model="addPerForm" :close-on-click-modal="false">
            <el-form :model="addForm" label-width="80px"  :rules="addFormRules" ref="addForm">
                <el-form-item label="权限名称" prop="authority">
                    <el-input v-model="addForm.authority" ></el-input>
                </el-form-item>
                <el-form-item label="权限描述" prop="description">
                    <el-input v-model="addForm.description" ></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addPerForm = false">取消</el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
            </div>
        </el-dialog>
        <!--编辑界面-->
        <el-dialog title="编辑权限" v-model="editPerForm" :close-on-click-modal="false">
            <el-form :model="editForm" label-width="80px"  :rules="editFormRules" ref="editForm">
                <el-form-item label="权限ID" prop="permissionId">
                    <el-input v-model="editForm.permissionId" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="权限名称" prop="authority">
                    <el-input v-model="editForm.authority"></el-input>
                </el-form-item>
                <el-form-item label="权限描述" prop="description">
                    <el-input v-model="editForm.description" ></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editPerForm = false">取消</el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
            </div>
        </el-dialog>
        <!--删除界面-->
    </section>
</template>
<script>
    import {getPers ,addPer ,deletePer ,updatePer} from '../../../api/api';
    import { mapGetters } from 'vuex'
    import { mapActions } from 'vuex'
    export default{
        data(){
            return{
                permissions:[],
                addPerForm:false,
                addLoading:false,
                editLoading:false,
                //添加角色数据
                addForm:{
                    permissionId:'',
                    description:''
                },
                addFormRules:{
                    authority:[
                        {required: true, message: '请输入一个权限名称'}
                    ],
                    description:[
                        {required: true, message: '请输入描述字符'}
                    ]
                },
                //编辑角色数据
                editPerForm:false,
                editForm:{
                    roleId:'',
                    roleName:'',
                    description:''
                },
                editFormRules:{
                    permissionId:[
                        {required: true, message: '请输入ID'}
                    ],
                    authority:[
                        {required: true, message: '请输入一个权限名称'}
                    ],
                    description:[
                        {required: true, message: '请输入描述字符'}
                    ]
                },
            }

        },
        methods:{
            ...mapActions([
                'setPers'
            ]),
            getAllPer(){
                getPers().then(res => {
                    this.permissions=res.data
                    this.setPers(
                        this.permissions
                    )
                    console.log(this.$store.state.permissions)
                })
            },
            addSubmit(){
                this.$refs.addForm.validate((valid) =>{
                    if(valid){
                        let param=this.addForm;
                        this.addLoading=true
                        addPer(param).then(res =>{
                            if(res.result=='OK'){
                                this.$message('保存成功=>'+res.info)
                                this.addLoading=false
                                this.addPerForm=false
                                this.getAllPer();
                            }else{
                                this.$message('保存失败=>'+res.info)
                                this.addLoading=false
                            }
                        })

                    }
                })
            },
            addRole(){
                this.addPerForm=true
            },
            EditRole(index , row){
                //console.log(row)
                this.editForm={
                    permissionId:row.permissionId,
                    authority:row.authority,
                    description:row.description
                }
                this.editPerForm=true;
            },
            editSubmit(){
                this.$refs.editForm.validate(valid =>{
                    if(valid){
                        let param=this.editForm
                        this.editLoading=true
                        updatePer(param).then(res =>{
                            if(res.result=='OK'){
                                this.$message('更新成功=>'+res.info)
                                this.editLoading=false
                                this.editPerForm=false
                                this.getAllPer();
                            }else{
                                this.$message('更新失败=>'+res.info)
                                this.editLoading=false
                                this.editPerForm=false
                            }
                        }).catch(res =>{

                        })
                    }
                })

            },

            deleteRole(index , row){
                this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deletePer(row.permissionId).then(res =>{
                        //console.log(res)
                        if(res.result=="OK"){
                            this.getAllPer();
                            this.$message({
                                type: 'success',
                                message: '成功删除！'
                            })

                        }else {
                            this.$message({
                                type: 'error',
                                message: '删除失败！'+res.info
                            })
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
        },
        mounted(){
            this.getAllPer()
        }
    }
</script>
<style>

</style>


