import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import Page6 from './views/nav3/Page6.vue'
import echarts from './views/charts/echarts.vue'
import UserTable from './views/user-manager/UserTable.vue'
import BatchManager from  './views/user-manager/BatchManager.vue'
import RolePermission from './views/user-manager/RolePermission.vue'
let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },

    //{ path: '/main', component: Main },
    {
       path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',
        leaf: true,
        children: [
            {path: '/', component: Main , name: '机构概览'}
        ]
    },
    {
        path: '/',
        component: Home,
        name: '用户管理',
        iconCls: 'el-icon-message',//图标样式class
        children: [
           // { path: '/', component: Main, name: '用户总揽', hidden: false },
            { path: '/user_info', component: UserTable, name: '用户详情' },
            { path: '/batch_manager', component: BatchManager, name: '批量管理' },
            { path: '/RolePermission', component: RolePermission, name: '角色权限管理' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '教师管理',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/page4', component: Table, name: '教师信息' },
            {
                path: '/page5', component: Page5, name: '教学信息',
                children:[
                    {
                        path: '/', component:Page4 , name: '二级路由'
                    },
                    {
                        path: '/edemed' ,component: UserTable , name: '嵌套路由'
                    }
                ]
            },
            { path: '/page6', component: Page5, name: '学生评价' }

        ]
    },
    {
        path: '/',
        component: Home,
        name: '学生管理',
        iconCls: 'fa fa-address-card',
        leaf: false,//只有一个节点
        children: [
            { path: '/page7', component: Page6, name: '学生信息6' },
            { path: '/page8', component: Table, name: '成绩管理5' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '教学资源管理',
        iconCls: 'fa fa-address-card',
        leaf: false,//只有一个节点
        children: [
            { path: '/page9', component: Page6, name: '学生信息4' },
            { path: '/page10', component: Table, name: '成绩管理3' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '统计分析',
        iconCls: 'fa fa-address-card',
        leaf: false,//只有一个节点
        children: [
            { path: '/page11', component: Page6, name: '学生信息1' },
            { path: '/page12', component: Table, name: '成绩管理2' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'Charts',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/echarts', component: echarts, name: 'echarts' }
        ]
    },
    /*//二级路由
    {
        path: '/page5',
        component: Home,
        name: '教学信息2',
        hidden:true,
        children: [
            {path: '/RolePermission1', component: UserTable , name: '机构概览3'}
        ]
    },*/
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    },

];

export default routes;


