<template>
    <section>
        <!-- 工具栏-->
        <el-row>
            <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
                <el-form :inline="true" >
                    <div style="float: right">
                        <el-form-item>
                            <el-input v-model="keyword" placeholder="姓名/用户ID/住址/号码"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" v-on:click="search" icon="search">查询</el-button>
                        </el-form-item>
                    </div>
                    <el-form-item style="float:left">
                        <el-button type="primary" @click="addUser">新增</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
                <el-table v-loading="listLoading"
                        :data="userInfoList"
                        border
                        tooltip-effect="dark"
                        style="width: 100%"
                        @selection-change="handleSelectionChange">
                    <el-table-column
                            type="selection"
                            width="55">
                    </el-table-column>
                    <el-table-column
                            label="用户ID"
                            width="100"
                            prop="userid"
                            sortable>
                    </el-table-column>
                    <el-table-column
                            prop="accountInfo.user_Name"
                            label="姓名"
                            width="100"
                            >
                    </el-table-column>
                    <el-table-column
                            prop="roleinfo.description"
                            label="角色"
                            width="100"
                            :filters="[{ text: '教师', value: '教师' }, { text: '学生', value: 2 },
                            { text: '管理员' ,value: 3}]"
                            :filter-method="roleFilter"
                            >
                    </el-table-column>
                    <el-table-column
                            prop="accountInfo.gender"
                            label="性别"
                            width="65"
                            >
                    </el-table-column>
                    <el-table-column
                            prop="accountInfo.address"
                            label="居住地"
                            width="150"
                            >
                    </el-table-column>
                    <el-table-column
                            prop="accountInfo.phoneNum"
                            label="联系方式"
                            width="150"
                            sortable
                    >
                    </el-table-column>
                    <el-table-column
                            prop="onlineState"
                            label="在线状态"
                            width="100"
                            show-overflow-tooltip>
                        <template scope="scope">
                            {{scope.row.onlineState?"在线":"离线"}}
                        </template>
                    </el-table-column>
                    <el-table-column
                            label="操作" >
                        <template scope="scope">
                            <el-button-group>
                                <el-button
                                        size="mini"
                                        type="success"
                                        @click="handleView(scope.$index, scope.row)">查看</el-button>
                                <el-button
                                        size="mini"
                                        type="warning"
                                        @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                                <el-button
                                        size="mini"
                                        type="danger"
                                        @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                            </el-button-group>
                        </template>

                    </el-table-column>
                </el-table>
                <!--工具条-->
                <el-col :span="24" class="toolbar">
                    <el-button type="danger" @click="batchRemove" :disabled="false">批量删除</el-button>
                    <el-button type="primary" @click="exportData" >导出数据</el-button>
                    <el-pagination layout=" total, prev, pager, next, jumper" @current-change="handleCurrentChange"  :page-size="size" :total="total" style="float:right;">
                    </el-pagination>
                </el-col>
            </el-col>
        </el-row>
        <!--新增界面-->
        <el-dialog title="注册用户" v-model="addUserForm" :close-on-click-modal="false">
            <el-form :model="addForm" label-width="80px"  :rules="addFormRules" ref="addForm">
                <el-form-item label="用户ID" prop="userId">
                    <el-input v-model="addForm.userId" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="用户角色" prop="roleid">
                    <el-select v-model="addForm.roleid" placeholder="请选择一个角色">
                        <el-option v-for="role in roles" :value="role.roleId" :label="role.description" :key="role.roleId" >
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input v-model="addForm.password" ></el-input>
                </el-form-item>

                <el-collapse >
                    <el-collapse-item title="填写用户详细信息" name="userinfo">
                        <el-form-item label="用户名称">
                            <el-input  v-model="addForm.accountInfo.user_Name"></el-input>
                        </el-form-item>
                        <el-form-item label="性别" prop="gender">
                            <el-radio-group v-model="addForm.accountInfo.gender">
                                <el-radio  label="男" ></el-radio>
                                <el-radio  label="女" ></el-radio>
                                <el-radio  label="保密"></el-radio>
                            </el-radio-group>
                        </el-form-item>
                        <el-form-item label="学校名称">
                            <el-input  v-model="addForm.accountInfo.schoolName"></el-input>
                        </el-form-item>
                        <el-form-item label="手机号码">
                            <el-input  v-model="addForm.accountInfo.phoneNum"></el-input>
                        </el-form-item>
                        <el-form-item label="地址">
                            <el-input type="textarea" v-model="addForm.accountInfo.address"></el-input>
                        </el-form-item>

                    </el-collapse-item>
                </el-collapse>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addUserForm = false">取消</el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
            </div>
        </el-dialog>
    </section>
</template>
<script>
    import NProgress from 'nprogress';
    import { getRoles ,userRegister ,getAllUserInfo ,getAllUserInfoPage ,deleteUser ,getAUserInfo ,
        saveUserinfo ,searchUserInfo ,updateUserInfo ,deleteUserInfo} from '../../api/api';
    export default{
        data(){
            return{
                roles:[],
                total:0,
                size:15,
                page: 0,
                keyword:'',
                userInfoList: [],
                multipleSelection: [],
                addLoading: false,
                listLoading: false,//列表数据加载
                //新增界面数据
                addFormRules: {
                    userId: [
                        { required: true, message: '请输注册账号(学号)', trigger: 'blur' }
                    ],
                    roleid:[
                        { required: true ,message: '必须为用户指定一个角色' },
                    ],
                    password:[
                        { required: true ,message: '请输入密码' ,trigger: 'blur'}
                    ],

                },
                addLoading:false,
                addUserForm: false,//新增界面是否显示
                addForm: {
                    userId: '',
                    roleid: '',
                    password: '',
                    accountInfo:{
                        schoolName:'',
                        user_Name:'',
                        address:'',
                        gender:"保密",
                        phoneNum:'',
                    },
                }

            }
        },
        methods:{

            addUser(){
                this.addUserForm = true;
                //获取用户角色列表
                getRoles().then(res => {
                    //console.log(res.data);
                    this.roles=res.data;
                })
            },
            addSubmit(){
                this.$refs.addForm.validate((valid) =>{
                    if(valid){
                        this.addLoading=true
                        //NProgress.start()
                        let param=this.addForm
                        userRegister(param).then(res => {
                            if(res.res=="OK"){
                                //注册成功
                                this.addUserForm=false;
                                this.addLoading=false
                                //NProgress.done();
                                JSON.stringify()
                                this.$message('注册成功！');
                                this.getUserInfo();
                            }
                            if(res.res=="ERROR"){
                                //this.addUserForm=false;
                                this.addLoading=false
                                //NProgress.done()
                                this.$message('注册失败，账号已被注册！');
                            }
                        }).catch(res =>{
                            //NProgress.done()
                            this.addLoading=false
                            this.$message('服务器异常，请稍候再试!');
                        })
                    }
                })
                console.log(JSON.stringify(this.addForm))

            },
            search(){
                let query=this.keyword;
                let param={
                    query:query,
                    size:this.size,
                    page:this.page
                }
                //this.listLoading = true;
                NProgress.start();
                searchUserInfo(param).then(res =>{
                    //console.log(res.data)
                    this.userInfoList=res.data.content
                    this.total=res.data.totalElements;
                    NProgress.done();
                    //this.listLoading = false;
                })
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            getUserInfo(){
                let param={
                    size:this.size,
                    page:this.page
                }
                NProgress.start();
                //this.listLoading = true;
                getAllUserInfoPage(param).then(res => {
                   // console.log(res.data)
                    this.userInfoList=res.data.content;
                    this.total=res.data.totalElements;
                    NProgress.done();
                    //this.listLoading = false;
                })

            },
            handleEdit(index, row) {
                console.log(index, row);
            },
            handleDelete(index, row) {
                console.log(index, row);
            },
            handleView(index, row){
                console.log(index, row);
            },
            handleCurrentChange(val) {
                console.log("val==="+val)
                this.page = val-1;
                this.getUserInfo();
            },
            batchRemove: function () {

            },
            exportData:function () {
                
            },
            roleFilter(value, row) {
                /*return row.tag === value;*/
                console.log(value)
                return true

            }

        },
        mounted() {
            this.getUserInfo();
        }
    }
</script>
<style>

</style>


