<template>
    <section>
        <el-row>
            <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
                <el-form :inline="true">
                    <el-form-item style="float:left">
                        <el-button type="primary" @click="addRole">新增</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
                <el-table
                        :data="roles"
                        border
                        style="width:100%">
                    <el-table-column
                            prop="roleId"
                            label="角色ID"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="roleName"
                            label="角色名称"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="description"
                            label="角色描述">
                    </el-table-column>
                    <el-table-column
                            label="操作" align="center">
                        <template scope="scope">
                            <el-button-group>
                                <el-button
                                        size="small"
                                        type="warning"
                                        @click="EditRole(scope.$index, scope.row)">编辑</el-button>
                                <el-button
                                        size="small"
                                        type="danger"
                                        @click="deleteRole(scope.$index, scope.row)">删除</el-button>
                            </el-button-group>
                            <el-button-group>
                                <el-button
                                        size="small"
                                        type="success"
                                        @click="viewPermission(scope.$index, scope.row)">查看权限</el-button>
                                <el-button
                                        size="small"
                                        type="primary"
                                        @click="distributeRole(scope.$index, scope.row)">分配权限</el-button>
                            </el-button-group>
                        </template>

                    </el-table-column>
                </el-table>

            </el-col>
        </el-row>
        <!--新增界面-->
        <el-dialog title="添加角色" v-model="addRoleForm" :close-on-click-modal="false">
            <el-form :model="addForm" label-width="80px"  :rules="addFormRules" ref="addForm">
                <el-form-item label="角色名称" prop="roleName">
                    <el-input v-model="addForm.roleName" ></el-input>
                </el-form-item>
                <el-form-item label="角色描述" prop="description">
                    <el-input v-model="addForm.description" ></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addRoleForm = false">取消</el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
            </div>
        </el-dialog>
        <!--编辑界面-->
        <el-dialog title="编辑角色" v-model="editRoleForm" :close-on-click-modal="false">
            <el-form :model="editForm" label-width="80px"  :rules="editFormRules" ref="editForm">
                <el-form-item label="角色ID" prop="roleId">
                    <el-input v-model="editForm.roleId" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="角色名称" prop="roleName">
                    <el-input v-model="editForm.roleName"></el-input>
                </el-form-item>
                <el-form-item label="角色描述" prop="description">
                    <el-input v-model="editForm.description" ></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editRoleForm = false">取消</el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
            </div>
        </el-dialog>
        <!--权限分配界面-->
        <el-dialog title="权限分配" v-model="disRoleForm" :close-on-click-modal="false">
            <p>选择赋予角色为<strong style="color: red">{{disRole.description}}</strong>的权限</p>
            <el-table :data="persLeft"
                      @selection-change="perSelected">
                <el-table-column
                        prop="permissionId"
                        label="权限ID"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="authority"
                        label="权限名称"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="description"
                        label="权限描述">
                </el-table-column>
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
            </el-table>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="disRoleForm = false">取消</el-button>
                <el-button type="primary" @click.native="addPerSubmit" :loading="editLoading">增加权限</el-button>
            </div>
        </el-dialog>
        <!--权限查看-->
        <el-dialog title="查看权限列表" v-model="viewPerTable" :close-on-click-modal="false">
            <el-table :data="permissions">
                <el-table-column
                        prop="permissionId"
                        label="权限ID"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="authority"
                        label="权限名称"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="description"
                        label="权限描述">
                </el-table-column>
                <el-table-column
                        label="操作"
                        width="80">
                    <template scope="scope">
                        <el-button
                                size="small"
                                type="danger"
                                @click="deletePer(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="viewPerTable = false">返回</el-button>
            </div>
        </el-dialog>
    </section>
</template>
<script>
    import {getRoles ,addRole ,deleteRole ,updateRole ,getPersByroleid
        ,deletePersFromRole ,addPersToRole} from '../../../api/api';
    import { mapGetters } from 'vuex'
    import { mapActions } from 'vuex'
    export default{
        computed:{
            ...mapGetters([
                'getPers',
                'getCount'
            ])
        },
        data(){
            return{
                roles:[],
                addRoleForm:false,
                addLoading:false,
                editLoading:false,
                //添加角色数据
                addForm:{
                    roleName:'',
                    description:''
                },
                addFormRules:{
                    roleName:[
                        {required: true, message: '请输入一个角色名称'}
                    ],
                    description:[
                        {required: true, message: '请输入描述字符'}
                    ]
                },
                //编辑角色数据
                editRoleForm:false,
                editForm:{
                    roleId:'',
                    roleName:'',
                    description:''
                },
                editFormRules:{
                    roleId:[
                        {required: true, message: '请输入ID'}
                    ],
                    roleName:[
                        {required: true, message: '请输入一个角色名称'}
                    ],
                    description:[
                        {required: true, message: '请输入描述字符'}
                    ]
                },
                //权限分配界面
                disRole:'',
                disRoleForm:false,
                persSelected:[],
                persLeft:[],
                //查看拥有的权限界面
                permissions:[],
                viewPerTable:false,


            }

        },

        methods:{
            getAllRoles(){
                getRoles().then(res => {
                    this.roles=res.data
                    //console.log(res)
                })
            },
            addSubmit(){
                this.$refs.addForm.validate((valid) =>{
                    if(valid){
                        let param=this.addForm;
                        this.addLoading=true
                        addRole(param).then(res =>{
                            if(res.result=='OK'){
                                this.$message('保存成功=>'+res.info)
                                this.addLoading=false
                                this.addRoleForm=false
                                this.getAllRoles();
                            }else{
                                this.$message('保存失败=>'+res.info)
                                this.addLoading=false
                            }
                        })

                    }
                })
            },
            addRole(){
                this.addRoleForm=true
            },
            EditRole(index , row){
                //console.log(row)
                this.editForm={
                    roleId:row.roleId,
                    roleName:row.roleName,
                    description:row.description
                }
                this.editRoleForm=true;
            },
            editSubmit(){
                this.$refs.editForm.validate(valid =>{
                    if(valid){
                        let param=this.editForm
                        this.editLoading=true
                        updateRole(param).then(res =>{
                            if(res.result=='OK'){
                                this.$message('更新成功=>'+res.info)
                                this.editLoading=false
                                this.editRoleForm=false
                                this.getAllRoles();
                            }else{
                                this.$message('更新失败=>'+res.info)
                                this.editLoading=false
                                this.editRoleForm=false
                            }
                        }).catch(res =>{

                        })
                    }
                })

            },

            deleteRole(index , row){
                this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deleteRole(row.roleId).then(res =>{
                       // console.log(res)
                        if(res.result=="OK"){
                            this.getAllRoles();
                            this.$message({
                               type: 'success',
                               message: '成功删除！'
                            })

                        }else {
                            this.$message({
                                type: 'error',
                                message: '删除失败！'+res.info
                            })
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            //分配权限（获取角色ID和权限列表）
            distributeRole(index , row){
                this.disRole=row;
                //不展示已拥有的权限
                //this.persLeft=Array.complement(this.permissions,this.getPers)
                this.persLeft=this.getPers;
                /*this.permissions=[]
                let param={
                    roleid:row.roleId
                }
                getPersByroleid(param).then(res =>{
                    for(let i in res){
                        let permission={
                            authority: res[i].authorityEntity.authority,
                            description: res[i].authorityEntity.description,
                           /!* rolePermissionId: res[i].rolePermissionId,*!/
                            permissionId: res[i].permissionId,
                        }
                        this.permissions.push(permission)
                    }
                })*/
                /*for (var i=0; i<this.persLeft.length; i++){
                    //对所有权限进行过滤
                    var tem_Permissionid=this.persLeft[i];
                    for (var j=0; j<this.permissions.length; j++){
                       // console.log("j层权限id"+this.permissions[j].permissionId)
                        if(tem_Permissionid == this.permissions[j].permissionId){
                            this.persLeft.splice(i,1)
                            console.log("两层id相等")
                        }
                    }
                }*/

                this.disRoleForm=true;
            },
            viewPermission(index ,row ){
                let param={
                    roleid:row.roleId
                }
                this.permissions=[]
                getPersByroleid(param).then(res =>{
                    for(var i in res){
                        let permission={
                            authority: res[i].authorityEntity.authority,
                            description: res[i].authorityEntity.description,
                            rolePermissionId: res[i].rolePermissionId,
                            permissionId: res[i].permissionId,
                        }
                        this.permissions.push(permission)
                    }
                })
                this.viewPerTable=true
            },
            deletePer(index , row){

                this.$confirm('此操作将永久删除该权限, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deletePersFromRole(row.rolePermissionId).then(res =>{
                       // console.log(res)
                        if(res.result=="OK"){
                            //删除this.permissions
                            this.permissions.splice(index,1)
                            this.$message({
                                type: 'success',
                                message: '成功删除！'
                            })

                        }else {
                            this.$message({
                                type: 'error',
                                message: '删除失败！'+res.info
                            })
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            perSelected(val){
                this.persSelected=val;
            },
            addPerSubmit(){
                let roleid=this.disRole.roleId;
                let params=this.persSelected;
                addPersToRole(roleid,params).then(res =>{
                    if(res.result=="OK"){
                        this.$message({
                            type: 'success',
                            message: '操作成功，'+res.info
                        })
                    }else{
                        this.$message({
                            type: 'error',
                            message: '操作失败！，'+res.info
                        })
                    }

                })
            },
            //取两个集合的补集
            /*complement(a ,b){
                return Array.minus(Array.union(a, b),Array.intersect(a, b));
            }*/
        },
        mounted(){
            this.getAllRoles()
        }
    }
    /*Array.complement = function(a, b){
        return Array.minus(Array.union(a, b),Array.intersect(a, b));
    };
    //交集
    Array.intersect = function(a, b){
        return a.uniquelize().each(function(o){return b.contains(o) ? o : null});
    };
    //差集
    Array.minus = function(a, b){
        return a.uniquelize().each(function(o){return b.contains(o) ? null : o});
    };
    //并集
    Array.union = function(a, b){
        return a.concat(b).uniquelize();
    };
    Array.prototype.contains = function(item){
        for(var i=0;i<this.length;i++){
            if(this[i].name==item.name){return true;}
        }
        return false;
    };
    Array.prototype.uniquelize = function(){
        var ra = new Array();
        for(var i = 0; i < this.length; i ++){
            if(!ra.contains(this[i])){//indexOf(“str”) != -1
                ra.push(this[i]);
            }
        }
        return ra;
    };
    Array.prototype.each = function(fn){
        fn = fn || Function.K;
        var a = [];
        var args = Array.prototype.slice.call(arguments, 1);
        for(var i = 0; i < this.length; i++){
            var res = fn.apply(this,[this[i],i].concat(args));
            if(res != null) a.push(res);
        }
        return a;
    };*/
</script>
<style>

</style>


