<template>
	<section class="chart-container">
		<el-row>
			<el-col :span="12">
				<div id="users" style="width:100%; height:400px;"></div>
			</el-col>
			<el-col :span="12" >
				<div id="online" style="width:100%; height:400px;"></div>
			</el-col>
		</el-row>
	</section>
</template>

<script>
    import echarts from 'echarts'
	export default {
        data() {
            return {
                users:null,
				online:null,

            }
        },
		mounted:function () {
            var _this = this;
            this.users=echarts.init(document.getElementById('users'))
			this.online=echarts.init(document.getElementById('online'))
			this.online.setOption(
                {
                    title: {
                        text: '用户在线情况',
                        /*subtext: '用户数量饼图',*/
                        x: 'left'
                    },
                    tooltip : {
                        trigger: 'axis',
                        axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                            type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                        }
                    },
                    legend: {
                        data:['利润', '支出', '收入']
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis : [
                        {
                            type : 'value'
                        }
                    ],
                    yAxis : [
                        {
                            type : 'category',
                            axisTick : {show: false},
                            data : ['周一','周二','周三','周四','周五','周六','周日']
                        }
                    ],
                    series : [
                        {
                            name:'利润',
                            type:'bar',
                            label: {
                                normal: {
                                    show: true,
                                    position: 'inside'
                                }
                            },
                            data:[200, 170, 240, 244, 200, 220, 210]
                        },
                        {
                            name:'收入',
                            type:'bar',
                            stack: '总量',
                            label: {
                                normal: {
                                    show: true
                                }
                            },
                            data:[320, 302, 341, 374, 390, 450, 420]
                        },
                        {
                            name:'支出',
                            type:'bar',
                            stack: '总量',
                            label: {
                                normal: {
                                    show: true,
                                    position: 'left'
                                }
                            },
                            data:[-120, -132, -101, -134, -190, -230, -210]
                        }
                    ]
                }
			)
			this.users.setOption({
                title: {
                    text: '用户总揽',
                    subtext: '用户数量饼图',
                    x: 'center'
                },
                tooltip: {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                legend: {
                    orient: 'vertical',
                    left: 'left',
                    data: ['站点管理员', '机构管理员', '教师', '学生', '培训师']
                },
                series: [
                    {
                        name: '用户数',
                        type: 'pie',
                        radius: '55%',
                        center: ['50%', '60%'],
                        data: [
                            { value: 335, name: '站点管理员' },
                            { value: 310, name: '机构管理员' },
                            { value: 234, name: '教师' },
                            { value: 135, name: '学生' },
                            { value: 1548, name: '培训师' }
                        ],
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }
                ]
            })

        }
    }

</script>

<style scoped>
	.chart-container {
		width: 100%;
		float: left;
	}
	/*.chart div {
        height: 400px;
        float: left;
    }*/

	.el-col {
		padding: 30px 20px;
	}
</style>


